* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 3rem;
  background-color: #dfdfdf;
}

h1, h2 {
  color: #333333;
}

.btn {
  font: inherit;
  padding: 0.5rem 1.5rem;
  cursor: pointer;
  border-radius: 4px;
  background-color: #800040;
  color: white;
  border: 1px solid #800040;
  margin: 0 1rem;
}

.btn:hover {
  background-color: #9c1458;
  border-color: #9c1458;
}

.btn--alt {
  background-color: transparent;
  color: #800040;
}

.btn--alt:hover {
  background-color: #f8dae9;
}

.card {
  background-color: white;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem;
  width: 20rem;
}

.actions {
  text-align: right;
}

.modal {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  background-color: white;
  padding: 1rem;
  text-align: center;
  width: 30rem;
  z-index: 10;
  position: fixed;
  top: 20vh;
  left: calc(50% - 15rem);
  max-height: 60rem;
  overflow-y: auto;
}


.backdrop {
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
}

.NewMeetupForm_form__vDFXx {
  padding: 1rem;
}

.NewMeetupForm_control__30BE_ {
  margin-bottom: 0.5rem;
}

.NewMeetupForm_control__30BE_ label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.NewMeetupForm_control__30BE_ input,
.NewMeetupForm_control__30BE_ textarea {
  display: block;
  font: inherit;
  border-radius: 4px;
  border: 1px solid #ccc;
  padding: 0.25rem;
  width: 100%;
}

.NewMeetupForm_actions__2Vnu0 {
  margin-top: 1rem;
  text-align: right;
}

.NewMeetupForm_actions__2Vnu0 button {
  font: inherit;
  cursor: pointer;
  background-color: #77002e;
  color: white;
  padding: 0.5rem 1.5rem;
  border: 1px solid #77002e;
  border-radius: 4px;
  font-weight: bold;
}

.NewMeetupForm_actions__2Vnu0 button:hover,
.NewMeetupForm_actions__2Vnu0 button:active {
  background-color: #a50e48;
  border-color: #a50e48;
}

.container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  grid-column-gap: 1rem;
}
.short-entry {
  font-size: 1.2em;
}
.description {
  font-size: 1.2em;
}

.container {
  padding: 1rem;
  width: 100%;
}

.header {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 30px;
  justify-items: left;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

h1 {
  margin: 0;
  text-align: center;
}

